.navItem {
    position: relative;
    left: 0;
    top: 0;
    width: 66px;
    height: 66px;
    text-align: center;
    transition: all 0.25s;
    &:hover {
        transform: scale(1.1);
        cursor: pointer;
        .title {
            display: inline-block;
        }
        .imgBox {
            background-color: #ffffff80;
        }
    }
    .imgBox {
        width: 40px;
        height: 40px;
        line-height: 52px;
        border-radius: 50%;
        background-color: #ffffff50;
        text-align: center;
        transform: translateX(-50%);
        position: relative;
        left: 50%;
        > img {
            width: 24px;
            height: 24px;
            border-radius: 12px;
            object-fit: cover;
        }
    }

    .title {
        font-size: 12px;
        color: white;
        text-shadow: 1px 1px 3px black;
        max-width: 66px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        display: none;
    }
}